export default{
	name:'navigation',
	data(){
		return{
			num:0,
			tabs:[
				{
					name:'宝贝'
				},
				{
					name:'详情'
				},
				{
					name:'评价'
				}
			]
		}
	},
	methods:{
		exchangeTab(index){
			this.num = index
			if(index === 0){
				wx.pageScrollTo({
					scrollTop:0
				})
			}else{
				this.$parent.tabScroll(index)
			}
		}
	}
}
